// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);

// Variables
@import "variables";


$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;


// @media (min-width: $breakpoint-md) {
//     〜
// }
// @media (min-width: $breakpoint-lg) {
//     〜
// }

body{
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS P Gothic", Verdana, sans-serif;
}

h1{
  font-size: 1.5rem;
}

///////////////////////////////
// 変数定義
//////////////////////////////

// $color-midnightblue: #001e43;
$color-midnightblue: #202f55;
$color-sidebar-hover: #f0f8ff;
$color-powderblue: #bccddb;
$color-steelblue: #4682b4;
$yy-line-height: 1rem * 1.5;

///////////////////////////////
// 一般
//////////////////////////////

.yy-fontsize-09 {
  font-size: 0.9rem;
}

.yy-fontsize-h5 {
  font-size: 1.25rem;
}

.yy-vertical-align-middle {
  display: table-cell;
  vertical-align: middle;
}

// cropperの画像をトリミングする際の、右側プレビューのサイズ
// レビューの画像サイズ
.yy-img-size {
  width: 128px;
  height: 128px;
}

.yy-timeline-review-img {
  display: inline-block;
  background-position: center center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  background-size: cover;
  @media (min-width: $breakpoint-md) {
      width: 100px;
      height: 100px;
  }
  @media (min-width: $breakpoint-lg) {
      width: 150px;
      height: 150px;
  }
}

.yy-avatar-img {
  display: inline-block;
  background-position: center center;
  background-repeat: no-repeat;
  margin: 5px;
  width: 200px;
  height: 200px;
  background-size: cover;
  border: 3px solid #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0,0,0,.15);
}

.yy-avatar-thumbnail-img {
  display: inline-block;
  background-position: center center;
  background-repeat: no-repeat;
  width: $yy-line-height * 0.9;
  height: $yy-line-height * 0.9;
  background-size: cover;
  border-radius: 5px;
}

.yy-overflow-hidden {
  overflow: hidden;
}

.yy-clicked {
  // bootstrapのdisabledとほぼ同じ。カーソルにnot-allowedをつけない版
  filter: alpha(opacity=65);
  -webkit-box-shadow: none;
  box-shadow: none;
  opacity: .65;
}

.yy-word-wrap {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.yy-review-word-wrap {
  line-height: $yy-line-height;
  max-height: $yy-line-height * 4;
  position: relative;
  overflow: hidden;

  &:before, &:after {
    position: absolute;
    background: #fff;
  }

  &:before {
    content: "･･･";
    bottom: 0;
    right: 0;
  }

  &:after {
    content: "";
    width: 100%;
    height: 100%;
  }
}

.yy-body {
  min-height: 100vh;
  padding-top: 4rem;
}

.yy-outline {
  border: 1px solid $color-steelblue;
  border-radius: 5px;
}

.yy-outline-top {
  border-top: 1px solid $color-steelblue;
}

.yy-outline-bottom {
  border-bottom: 1px solid $color-steelblue;
}


.yy-bg-white {
  background-color: white !important;
}


.yy-bg-midnightblue {
    background-color: $color-midnightblue !important;
}




.yy-bg-powderblue {
  background-color: $color-powderblue !important;
}

.yy-bg-test {
  background-color: $color-steelblue !important;
}

.yy-bg-body {
  background: linear-gradient(to left, #f0f8ff, white 3%, white 97%, #f0f8ff);
}

.yy-bg-sidebar {

  background-color: white !important;

  &.active {
    color: black !important;
    border-left: 2px solid $color-midnightblue;
  }

  &:focus, &:hover {
    background-color: $color-sidebar-hover !important;
  }
}
.yy-pointer{
  cursor: pointer;
}

.yy-legal h1, .yy-privacy h1, .yy-about h1 {
  margin-bottom:30px;
  margin-top:30px;
}

.yy-legal h2, .yy-privacy h2, .yy-about h2 {
  font-size: 16px;
}

.yy-about h2 {
  font-weight:bold;
}

.yy-privacy h3 {
  font-weight: bold;
  font-size: 100%;
}

.yy-about h3 {
    padding-left: 16px;
    border-left: 4px solid #ddd;
    font-weight: bold;
    font-size: 14px;
}

.yy-legal p, .yy-legal ol, .yy-privacy p, .yy-privacy ol, .yy-about p, .yy-about ul {
  font-size: 12px;
}

.yy-legal p, .yy-privacy p, .yy-about p {
  margin-bottom: 0.5rem;
}

.yy-privacy ol {
  list-style-type: none;
}

.yy-privacy ol>li {
  counter-increment: cnt;
}

.yy-privacy ol>li:before {
  content: "(" counter(cnt) ") ";
}

.yy-emphasis {
    background-color: #ffdb5d;
    font-weight: bold;
}

.yy-notifications{
  font-size:80%;
}

.yy-unreadnotification-mark{
  margin:auto;
  color:red;
  font-size:10%;
}

.yy-review-url-image {
  max-height: 64px;
  max-width: 64px;
}

.yy-review-url-title {
  font-weight: bold;
  font-size: 13px;
}

.yy-review-url-description {
  font-weight: lighter;
  font-size: 11px;
}

.yy-review-url-ogp {
  border: solid 1px #e6ecf0;
  border-radius: 4px;
  padding: 10px;
}

a.yy-review-url-link {
  text-decoration: none;
  color: black;
}
a.yy-review-url-link:hover {
  text-decoration: underline;
  color: black;
}

span.yy-review-domain {
  font-size: 6px;
  color: #00afcc;
}

.yy-post-annotation {
  color:red;
  font-size:10px;
}

// boostrap4のbugでdoropdown-menu-rightが正常に動かないので上書き。
// 参考：https://stackoverflow.com/questions/43867258/bootstrap-4-navbar-dropdown-menu-right
.dropdown-menu-right {
    right: 0;
    left: auto;
}
